import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom';
import * as baseActions from 'store/modules/base';
import * as userActions from 'store/modules/user';
import * as postActions from 'store/modules/post';
import MypageForm from 'components/mypage/MypageForm';
import UserMenuContainer from 'containers/common/UserMenuContainer';

class MypageContainer extends Component {

    state = {
        checkingPermission: true,
        username: ''
    };

    componentDidMount() {
        this.checkPermission();
        this.getUserProfile();
        this.getMyImages();
    }

    checkPermission = async () => {
        const username = localStorage.getItem('username');
        const token = localStorage.getItem('token');

        const { BaseActions } = this.props;
        const config = {
            headers: {
                'Authorization': `JWT ${token}`
            }
        }
        try {
            await BaseActions.checkLogged(config);
            if (this.props.loggedUsername !== username) {
                alert("자신의 페이지가 아니네요..!");
                window.location.href = "/";
                return;
            }
            this.setState({
                checkingPermission: false,
                username: username
            });
        } catch (e) {
            alert("로그인을 해주세요...");
            window.location.href = "/login";
        }
    }

    getUserProfile = async () => {
        const { UserActions } = this.props;
        const username = await localStorage.getItem("username");
        const token = await localStorage.getItem('token');

        const config = {
            headers: {
                'Authorization': `JWT ${token}`
            }
        };
        try {
            await UserActions.getUserProfile({ username }, config);
        } catch (e) {
            console.log(e);
        }
    }

    getMyImages = async () => {
        const { PostActions } = this.props;

        const token = await localStorage.getItem('token');

        const config = {
            headers: {
                'Authorization': `JWT ${token}`
            }
        }

        try {
            await PostActions.getPosts(config);
        } catch (e) {
            console.log(e);
        }
    }

    showProfileMenu = () => {
        const { BaseActions } = this.props;

        BaseActions.showProfileMenu();
    }

    render() {
        const { profile, posts } = this.props;
        const { showProfileMenu } = this;
        return (
            <React.Fragment>
                <MypageForm
                    loading={this.state.checkingPermission}
                    username={this.state.username}
                    profile={profile}
                    posts={posts}
                    showProfileMenu={showProfileMenu} />
                <UserMenuContainer />
            </React.Fragment>
        );
    }
}

export default connect(
    (state) => ({
        loggedUsername: state.base.get('loggedUsername'),
        profile: state.user.get('profile'),
        posts: state.post.get('posts')
    }),
    (dispatch) => ({
        BaseActions: bindActionCreators(baseActions, dispatch),
        UserActions: bindActionCreators(userActions, dispatch),
        PostActions: bindActionCreators(postActions, dispatch)
    })
)(withRouter(MypageContainer));


