import React, { Component } from 'react';
import styles from './PageHeader.scss';
import classNames from 'classnames/bind';
import { Link } from 'react-router-dom';

const cx = classNames.bind(styles);

class PageHeader extends Component {
  render() {
    const { logged, onLogout } = this.props;

    return (
      <div className={cx('page-header')}>
        <div className={cx('logo-line')}>
          <div className={cx('logo')}>
            <Link to="/" className={cx('text')}>
              Gallery
            </Link>
          </div>
        </div>
        <div className={cx('menu-line')}>
          <Link to="/" className={cx('menu')}>
            Home
          </Link>
          <Link to="/search" className={cx('menu')}>
            Search
          </Link>
          {
            logged ? (
              [
                <Link key="upload" to="/editor" className={cx('menu')}>
                  Upload
                </Link>,
                <Link key="mypage" to="/mypage" className={cx('menu')}>
                  MyPage
                </Link>,
                <div key="logout" onClick={onLogout} className={cx('menu')}>
                  Logout
                </div>
              ]
            ) :
              (
                <Link to="/login" className={cx('menu')}>
                  Login / Register
                </Link>
              )
          }
        </div>
      </div>
    );
  }
}

export default PageHeader;


