import React from 'react';
import styles from './ImageDisplay.scss';
import classNames from 'classnames/bind';
import { POINT_CONVERSION_COMPRESSED } from 'constants';

const cx = classNames.bind(styles);

const ImageDisplay = ({posts}) => {

  let oneLineArray = [];
  let wholeArray = [];
  
  let displayArray = [];

  const array1 = posts.toJS().map((post, i) => {
    
    oneLineArray.push(
      <div className={cx('oneImage')} key={i}>
        <img src={post.image} />
      </div>
    );
    if((i + 1) % 3 === 0) {
      wholeArray.push(oneLineArray);
      oneLineArray = [];
    }

    return wholeArray;
  });

  const array2 = wholeArray.map((element, i) => {
    displayArray.push(
      <div className={cx('oneLine')} key={i}>
        {
          element
        }
      </div>
    );
      
    // return displayArray;
    
  });

  // console.log(displayArray);
  

  return (
    <div className={cx('my-images')}>
 
    {
      displayArray
    }

    </div>
  )
}

export default ImageDisplay;


